/* font-family: "OpenSansRegular"; */
@font-face {
  font-family: "OpenSansRegular";
  src: url("../fonts/OpenSansRegular/OpenSansRegular.eot?#iefix") format("embedded-opentype"), url("../fonts/OpenSansRegular/OpenSansRegular.woff") format("woff"), url("../fonts/OpenSansRegular/OpenSansRegular.ttf") format("truetype");
  font-style: normal;
  font-weight: normal;
  font-display: fallback;
}

html {
  width: 100%;
  /* height: 100%; */
  font-size: 16px;
  line-height: 1.42;
  margin: 0;
  padding: 0;
  -webkit-backface-visibility: hidden;
  /* overflow-x: hidden; */
}

body {
  font-size: 16px;
  min-width: 320px;
  opacity: 1;
  position: relative;
  line-height: 1.42;
  font-family: "OpenSansRegular", sans-serif;
  overflow-x: hidden;
  color: #000000;
}

body input, body textarea {
  border: #666 1px solid;
  outline: none;
}

body input:focus:required:invalid, body textarea:focus:required:invalid {
  border-color: red;
}

body input:required:valid, body textarea:required:valid {
  border-color: green;
}

.img-responsive {
  display: block;
  max-width: 100%;
  height: auto;
}

.img-responsive.img-width {
  width: 100%;
}

.header_fon {
  min-height: 700px;
  display: grid;
  justify-content: center;
  align-items: center;
  position: relative;
  padding-bottom: 200px;
}

.header_fon:before {
  top: 0px;
  left: 0;
  width: 100%;
  height: 100%;
  position: absolute;
  background: -webkit-linear-gradient(25deg, orange, #996300);
  background: -o-linear-gradient(25deg, orange, #996300);
  background: linear-gradient(65deg, orange, #996300);
  border-radius: 0 0 50% 50% / 0 0 50% 50%;
  opacity: .8;
  z-index: -1;
  transform: scaleX(1.4);
  z-index: 1;
  content: '';
}

.header_fon_content {
  color: #000;
  z-index: 1;
  text-align: center;
}

.header_fon_content h1 {
  color: #000;
}

.grid_ones {
  display: grid;
  border: 1px solid MediumSpringGreen;
  grid-template-columns: 35% 15% 387px auto;
  grid-gap: 0.5rem;
}

.grid_ones_items {
  padding: 10px;
  background-color: brown;
  border: 1px solid orange;
  color: #fff;
}

.grid_ones_items:nth-child(2n+2) {
  color: #000;
}

.grid_twos {
  width: 100%;
  display: grid;
  border: 1px solid red;
  grid-template-columns: 33% 15% 38% 11%;
  grid-gap: 1%;
}

.grid_twos_items {
  background-color: #eee;
  border: 1px solid orange;
  padding: 10px;
}

.grid_trys {
  display: grid;
  border: 1px solid red;
  grid-template-columns: repeat(4, 1fr);
  grid-auto-rows: 250px;
  grid-auto-rows: minmax(250px, auto);
  grid-gap: .5rem;
}

.grid_trys_items {
  background-color: #eee;
  border: 1px solid orange;
  padding: 10px;
}

.grid_fifth {
  display: grid;
