.check_grid_one {
  display: grid;
  border: 1px solid purple;
  padding: 10px;
  grid-template-columns: repeat(4, calc(25% - (1 * 10px)));
  grid-auto-rows: 1fr;
  grid-gap: 10px;
  justify-content: center;
  align-content: center;
}

.check_grid_one_item {
  padding: 10px;
  border: 1px dashed blue;
