.check_grid_try {
  min-width: 320px;
  margin: 0 auto 1px;
  min-height: 100vh;
  display: grid;
  grid-gap: 10px;
  grid-template: 100px 1fr 100px 140px 150px auto / 230px 1fr 230px;
  grid-template-areas: "header header header" "left-sidebar main right-sidebar" "footer footer footer " "poplic  poplic poplic2" "poplic3 poplic4 poplic2" ". poplic5 .";
}

.check_grid_try_item {
  border: 2px solid blue;
}

.check_grid_try_item:nth-child(1) {
  grid-area: header;
}

.check_grid_try_item:nth-child(2) {
  grid-area: left-sidebar;
}

.check_grid_try_item:nth-child(3) {
  grid-area: main;
}

.check_grid_try_item:nth-child(4) {
  grid-area: right-sidebar;
}

.check_grid_try_item:nth-child(5) {
  grid-area: footer;
}

.check_grid_try_item:nth-child(6) {
  grid-area: poplic;
  background-color: gold;
}

.check_grid_try_item:nth-child(7) {
  grid-area: poplic2;
}

.check_grid_try_item:nth-child(8) {
  grid-area: poplic3;
}

.check_grid_try_item:nth-child(9) {
  grid-area: poplic4;
}

.check_grid_try_item:nth-child(10) {
  grid-area: poplic5;
}

@media only screen and (max-width: 750px) {
  .check_grid_try {
    grid-template: 100px auto auto auto 100px / 1fr;
    grid-template-areas: "header" "main" "left-sidebar" "right-sidebar" "footer";
  }
}

