.griding_check {
  margin: 0;
  min-height: 100%;
  min-height: 100vh;
  display: grid;
  grid-template-columns: 1fr 3fr 1fr;
  grid-template-rows: 50px auto 100px;
  grid-template-areas: 'header header header' 'nav main aside' 'footer footer footer';
  grid-column-gap: 10px;
  grid-row-gap: 15px;
  padding: 10px;
  box-sizing: border-box;
}

.griding {
  border: 1px solid #e4e4e4;
  text-align: center;
}

.griding_check .header {
  grid-area: header;
  background: linear-gradient(to bottom, rgba(245, 50, 252, 0.5) 0%, rgba(91, 222, 255, 0.5) 100%);
}

.griding_check .main {
  padding: 10px;
  grid-area: main;
  background: linear-gradient(to bottom, rgba(135, 255, 181, 0.5) 0%, rgba(249, 89, 231, 0.5) 100%);
}

.griding_check .nav {
  grid-area: nav;
  background: linear-gradient(to bottom, rgba(135, 255, 181, 0.5) 0%, rgba(249, 89, 231, 0.5) 100%);
}

.griding_check .aside {
  grid-area: aside;
  background: linear-gradient(to bottom, rgba(135, 255, 181, 0.5) 0%, rgba(249, 89, 231, 0.5) 100%);
}

.griding_check .footer {
  grid-area: footer;
  background: linear-gradient(to top, rgba(245, 50, 252, 0.5) 0%, rgba(91, 222, 255, 0.5) 100%);
}

