  font-size: 1.75em;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  border: 0.1em solid var(--port-item);
}

.portfolio-item:hover {
  opacity: .51;
}

.portfolio-item a {
  color: var(--port-item);
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  color: red;
}

