



.portfolio_block3 {

	display: grid;
	grid-template-columns:repeat(6,[col] 1fr);
	grid-template-rows:repeat(3,[row] 1fr);

//rid-column-gap: 10px;
//grid-row-gap: 10px;
///grid-gap:20px;
//grid-row-gap: 10px;
}

.portfolio_items3 {
	margin: 5px;
	border:2px solid red;
	min-height: 5vh;
	background-position: center;
	background-size: cover;
	text-align: center;
	align-content: center;
	font-size: 22px;
	color:#fff;
	font-weight: bold;
	position: relative;
	@include transition(color, 0.2s, ease);
	cursor: pointer;
}

.portfolio_items3 {
	min-height: 150px;
}

.portfolio_items3:nth-child(1) {
// grid-column: col 1 / col 3;
grid-column: span 2;
}
.portfolio_items3:nth-child(2) {

	grid-column: span 2;
	grid-row: span 2;

//grid-column: col 3 / col 5;
//grid-row: row 1 / row 3;



}
.portfolio_items3:nth-child(3) {
// grid-column: col 1 / col 3;
grid-column: span 1;
grid-row: span 2;
}
.portfolio_items3:nth-child(4) {
// grid-column: col 1 / col 3;
grid-column: span 1;
}
.portfolio_items3:nth-child(9) {
// grid-column: col 1 / col 3;
grid-column: span 2;

}
.portfolio_items3:nth-child(10) {
// grid-column: col 1 / col 3;
grid-column: span 2;

}


